:root {
  --background: #F3F3F3; /* Light mode background */
  --foreground: #070707; /* Light mode foreground */
  --primary: #070707;    /* Light mode primary color */
  --secondary: #3FA7D6;  /* Light mode secondary color */
  --accent: #287EBB;     /* Light mode accent color */
  --text: #070707;
  --navigation: #E1E1E1; /* Light mode text color */
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: #070707; /* Dark mode background */
    --foreground: #F3F3F3; /* Dark mode foreground */
    --primary: #F3F3F3;    /* Dark mode primary color */
    --secondary: #3FA7D6;  /* Dark mode secondary color */
    --accent: #287EBB;     /* Dark mode accent color */
    --text: #F3F3F3;       /* Dark mode text color */
    --navigation: #0D1B2A;
  }
}

body {
  color: var(--text);
  background-color: var(--background);
  font-family: Arial, sans-serif;
}

a {
